import React from 'react';
import { Divider, Carousel } from 'antd';
import './index.less';

function Index(props) {
    return (
        <div>
            <Divider orientation="left"></Divider>
            <Carousel autoplay>
                {
                    props.recommendedArticleArr.map(item => (
                        <div style={{ background: '#364d79' }}>
                            <p className={'recommendedArticles-title line-ellipsis'}>
                                <a href={item.link}>{item.name}</a>
                            </p>
                            <p className={'recommendedArticles-content line-ellipsis-4'}>
                                {item.description || '... ...'}
                            </p>
                        </div>
                    ))
                }
            </Carousel>
        </div>
    );
}

export default Index;



