import React, {useEffect} from 'react';
import { connect } from 'react-redux';
import { Divider, Table } from "antd";
import { changLoadingState } from '../../actions/global';
import RecommendedArticles from '../../components/RecommendedArticles';
import { recommendedArticleArr, allBlogLink } from '../../utils/enums';

const mapState = state => ({
    loading: state.global.isLoading,
});
function Index(props) {
    const columns = [
        {
            title: '',
            dataIndex: 'id',
            key: 'id',
            width: 80,
        }, {
            title: '',
            dataIndex: 'name',
            key: 'name',
            render: (text, item) => <a href={item.link}>{text}</a>,
        },
    ];
    useEffect(() => {
        props.changLoadingState();
        return () => props.changLoadingState();
    }, [])

    return (
        <div>
            <RecommendedArticles
                recommendedArticleArr={recommendedArticleArr}
            />
            <div style={{marginTop: 40}}></div>
            <Divider orientation="left"></Divider>
            <Table columns={columns} dataSource={allBlogLink} />
        </div>
    );
}

export default connect(mapState, { changLoadingState })(Index);



